<template>
  <div id="user">
    <keep-alive>
      <router-view/>
    </keep-alive>
    <main-tab-bar/>
  </div>
</template>

<script>

  import {connectionSocket} from "../../utils/websocket";
  import MainTabBar from "../../components/mainTabBar/MainTabBar";

  export default {
    name: "User",
    components: {
      MainTabBar
    },
    data() {
      return {
        myAva: ''
      }
    },
    mounted() {
      connectionSocket()
      this.myAvatar = this.$route.query.myAva
      this.$router.push({
        path: '/user/list',
        query: {
          myAva: this.myAvatar
        }
      })
    }
  }
</script>

<style scoped>

</style>



